
.new-content{
    width:100%;
}
.questionnaire-header, .questionnaire-body, .questionnaire-footer{
    width:100%;
    text-align: center;
    background:#fff;
    padding:20px;
}
.questionnaire-body{
    border-top:1px solid #c0c0c0;
    border-bottom: 1px solid #c0c0c0;
    box-sizing: border-box;
}
.questionnaire-title{
    font-size: 20px;
}
.question-box{
    width:100%;
    min-height:150px;
    padding:10px;
    /*box-sizing: border-box;*/
    background:#fff;
    text-align: left;
    border:1px solid #c0c0c0;
    margin-bottom:20px;
}
.question-box:hover{
    background: rgb(254,241,232);
}
.question-box .question-choice-group{
    width:100%;
    margin-top:15px;
    padding-left:40px;
    box-sizing: border-box;
}
.question-box .question-choice-group input{
    margin-right:20px;
}
.question-box .required-icon{
    color:red;
    margin-left:10px;
}
.questionnaire-footer .ques-btn{
    padding:3px 10px;
    font-size: 16px;
    background: #fff;
    font-family: "微软雅黑";
    color:#fff;
    border-radius: 3px;
    border:1px solid rgb(213, 214, 203);
    background:rgb(247, 120, 2);
}
.question-echarts{
    width: 400px;
    height:150px;
    float:right;
}
.question-box textarea{
    width:50%;
    height:120px;
    margin:15px 0 0 40px;
    font-size: 16px;
}



