
.questionnaire-add-question{
    border:1px solid #dddddd;
    margin-top:20px;
}
.question{
    width:100%;
    min-height:120px;
    padding:10px 20px 40px 20px;
    box-sizing: border-box;
    background:#fff;
    text-align: left;
}
.question:hover{
    background: rgb(254,241,232);
}
.question input[type="text"]{
    width:80%;
    height:1.5em;
    font-size: 16px;
    line-height: 1.5em;
    border:none;
    margin-left:20px;
    background:transparent;
}
.question label.required-checkbox{
    float:right;
}
.question textarea{
    width:70%;
    height:120px;
    margin:15px 0 0 40px;
    font-size: 16px;
}
.question .question-choice-group{
    width:100%;
    margin-top:5px;
    padding-left:40px;
    box-sizing: border-box;
}
.question span.delete-option{
    width:2em;
    height:2em;
    font-size: 18px;
    font-weight: bold;
    color:red;
    cursor: pointer;
}
.question-actions{
    float:right;
    width:55%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    margin:10px 0 10px 0;
}
.question span.add-option{
    display: inline-block;
    width:20%;
    height:1em;
    font-size: 30px;
    line-height: 1em;
    text-align: center;
    border-radius: 3px;
    color:#fff;
    float:left;
    background:rgb(247, 120, 2);
    cursor: pointer;
}
.question span.hide-add-option{
    background: transparent;
}
.question-actions span.question-action{
    cursor: pointer;
    font-size: 16px;
}



