
.new-content{
    width:100%;
}
.questionnaire-header, .questionnaire-body, .questionnaire-footer{
    width:100%;
    text-align: center;
    background:#fff;
    padding:20px;
    box-sizing:border-box;
}
.questionnaire-header input{
    width:100%;
    height:3em;
    line-height: 3em;
    font-size: 20px;
    font-weight: bold;
    border:none;
    text-align: center;
}
.questionnaire-header input:hover{
    background:rgb(254,241,232);
}
.questionnaire-body{
    border-top:1px solid #c0c0c0;
    border-bottom: 1px solid #c0c0c0;
    box-sizing: border-box;
}
.questionnaire-body .questionnaire-add-question{
    border:1px solid #dddddd;
    margin-top:20px;
}
.questionnaire-body .add-btn{
    height:60px;
    background:rgb(239,239,239);
    font-size: 14px;
    line-height: 60px;
    cursor: pointer;
}
.question-type{
    width:100%;
    height:60px;
    background:#fff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack:center;
        justify-content:center;
    -ms-flex-align: center;
        align-items: center;
    transition: height 3s;
    -moz-transition:height 3s;
    -webkit-transition: height 3s;
    -ms-transition: height 3s;
    -o-transition: height 3s;
    display: none;
}
.question-type .type-btn, .questionnaire-footer .questionnaire-btn{
    width:80px;
    height:30px;
    background:rgb(239,239,239);
    color:#000;
    font-size: 12px;
    margin:0 20px;
    cursor: pointer;
    border-radius: 3px;
    border:1px solid #c0c0c0;
}
.questionnaire-footer .publish-btn{
    color:#fff;
    background: rgb(247, 120, 2)
}
.question-type .type-btn:hover{
    color:#fff;
    background:rgb(247,120,2);
}



