
.page-wrapper{
    text-align: center;
    font-size: 0;
}
.page-item{
    list-style: none;
    display: inline-block;
    font-size: 16px;
    border: 1px solid #6E9DE4;
    margin-right:5px;
}
.page-item a{
    display: block;
    padding:5px 10px;
    text-decoration: none;
    cursor: pointer;
    background:#F5F5F5;
    color: #6E9DE4;
}
.page-item a:hover{
    background:#E0E0E0;
    color: #1928DE;
}
.page-item .active{
    color: #000;
    cursor: not-allowed;
}



