
.newnaire-btn{
    display: block;
    width:9em;
    height:2em;
    line-height: 2em;
    font-size: 24px;
    background:rgb(247, 120, 2);
    color:#fff;
    border-radius: 3px;
    text-align: center;
    text-decoration: none;
    /*margin:100px auto;*/
}
.list-wrapper, table,thead,tbody,tr{
    width:100%;
}
th{
    text-align: left;
    height:40px;
    vertical-align: middle;
}
td{
    height:40px;
    vertical-align: middle;
    color:#000;
}
.td-title{
    width:30%;
}
.td-time{
    width:15%;
}
.td-status{
    width:15%;
}
td.inpublish{
    color:#00ff00;
}
td.finished{
    color:red;
}
td input[type=checkbox]{
    margin:0 5px 0 20px;
}
.checkbox-td{
    width:70px;
}
tbody tr:not(:last-child){
    background: #fff;
    border-bottom: 2px solid rgb(239,239,239);
}
tbody tr:not(:last-child):hover{
background: rgb(252,240,229);
}
tr button.deletechecked{
    margin-left:10px;
}
.new-questionnaire-btn{
    width:100px;
    height:20px;
    line-height: 20px;
    font-size: 14px;
    background:rgb(247, 120, 2);
    color:#fff;
    border-radius: 3px;
    float:right;
    text-align: center;
    text-decoration: none;
    margin-right:30px;
}
.operators-btn{
    width:5em;
    height:20px;
    line-height: 20px;
    font-size: 14px;
    background: #fff;
    font-family: "微软雅黑";
    color:#000;
    border-radius: 3px;
    border:1px solid rgb(213, 214, 203);
    display: inline-block;
    text-decoration: none;
    text-align: center;
    cursor: pointer;
}
tbody .link:hover, tbody .link:active{
    background:rgb(247, 120, 2);
    color:#fff;
}



